import React, {Component} from 'react'
import {connect} from 'react-redux'
import * as R from 'ramda'
import {Link} from 'react-router-dom'

import {fetchBeers, loadMoreBeers} from 'actions'
import {getBeers} from 'selectors'

class Beers extends Component {

    componentDidMount () {
        this.props.fetchBeers()
        window.addEventListener('scroll', this.updateDimensions.bind(this));
        
    }
    
    // Infinite Scroll, defining height of the window
    updateDimensions () {
        var w = window.innerWidth;
        var h = window.innerHeight;

        const windowHeight = 'innerHeight' in window ? window.innerHeight : document.documentElement.offsetHeight;
        const body = document.body;
        const html = document.documentElement;
        const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight);
        const windowBottom = windowHeight + window.pageYOffset;

        if (windowBottom >= docHeight) {
            console.log('You reached the bottom. The height is ' + h + ', and the width is ' + w);
            this.props.loadMoreBeers();
        }
    }

    // Rendering each beer
    renderBeer (beer, index) {
        const shortName = `${R.take(20, beer.name)}`
        const shortTagline = `${R.take(30, beer.tagline)}`
        
        return (
            <div className='col-sm-4' key={index}>
                <Link className='thumbnail' key={beer.id} to={{pathname: `/${beer.id}`, state: {modal: true}}}>
                    <img className='img-thumbnail' src={beer.image_url} alt={beer.name} />
                    <div className='caption'>
                        <h2>{shortName}</h2>
                        <p>{shortTagline}</p>
                    </div> 
                </Link>
            </div>
        )
    }

    // Rendering Primary page
    render () {
        const {beers} = this.props
        return (
            <div className='container'>
                <div className='row'>
                    <div className='col-md-12'>
                        <div className='header row'>
                            <h2>Beer</h2>
                            <h2>Guru</h2>
                        </div>
                        <div className='books row'>
                            {beers && beers.map((beer, index) => this.renderBeer(beer, index))}
                        </div>
                    </div>
                </div>
                <div className='spinner-container'>
                    <div className='spinner'>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        beers: getBeers(state)
    }
}

const mapDispatchToProps = {
    fetchBeers,
    loadMoreBeers
}

export default connect(mapStateToProps, mapDispatchToProps)(Beers)


