import request from 'superagent'

export const fetchBeers = async () => {
    const {body} = await request.get(
        `https://api.punkapi.com/v2/beers?page=1&per_page=12`
    )
    return body
}

export const loadMoreBeers = async ({offset}) => {
    const page = offset / 12 + 1 // it is divided by 15, because in url(params) we have 15 items per page
    const {body} = await request.get(
        `https://api.punkapi.com/v2/beers?page=${page}&per_page=12`
    )
    return body
}

export const fetchBeerById = async id => {
    const {body} = await request.get(
        `https://api.punkapi.com/v2/beers/${id}`
    )
    const { 0: beer } = body
    return beer
}


