import React, {Component} from 'react'
import {connect} from 'react-redux'

import {fetchBeerById} from 'actions'
import {getBeerById} from 'selectors'

class Modal extends Component {
    constructor(props){
        super(props);
        this.state = {
            isLoading: true
        }
    }

    componentDidMount () {
        this.props.fetchBeerById(this.props.match.params.id)
    }

    foodRender (item) {
        return (
            <p>{item}</p>
        )
    }

    renderDetails () {
        const {beer} = this.props
        const stop = (e) => {
            e.stopPropagation()
        }
        return (
            <div className='description_modal' onClick={stop}>
                    <div className='row'>
                        <div className='col-sm-3'>
                            <div className='details-thumbnail'>
                                <img className='img-details' src={beer.image_url} alt='No img :('/>
                            </div>
                        </div>
                        <div className='col-sm-9'>
                            <div className='details-header'>
                                <h3>{beer.name}</h3>
                                <p>{beer.tagline}</p>
                            </div>
                            <div className='details-properties'>
                                <span className='properties'><strong>IBU: </strong>{beer.ibu}</span>
                                <span className='properties'><strong>ABV: </strong>{beer.abv}%</span>
                                <span className='properties'><strong>EBC: </strong>{beer.ebc}</span>
                            </div>
                            <div className='details-description'>
                                <p>{beer.description}</p>
                            </div>
                            <div className='details-bottom'>
                                <p><strong>Best served with:</strong></p>
                                {beer.food_pairing.map((element, index) =>
                                    <p key={index}>- {element}</p>
                                )}
                            </div>
                        </div>
                    </div>
            </div>
        )
    }

    render () {
        console.log(this.props.beer)
        const {beer} = this.props
        const back = (e) => {
            e.stopPropagation()
            this.props.history.goBack()
        }
        return (
            <div 
            className='modal-background'
            onClick={back}
            >
                <div className='container'>
                    <div className='row'>
                        <div className='col-md-12'>
                            {beer && this.renderDetails()}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    beer: getBeerById(state, state.detailsPage.id)
})

const mapDispatchToProps = {
    fetchBeerById
}

export default connect(mapStateToProps, mapDispatchToProps)(Modal)



